import styled, {useTheme} from 'styled-components';
import {GithubIcon, Button} from '../components/atoms';
import Typography from '../components/atoms/typography';
import GithubRepository from '../repository/github';
import {useNavigate} from 'react-router-dom';

const GITHUB_END_POINT = `https://github.com/login/oauth/authorize?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&scope=repo:status read:repo_hook user:email&redirect_uri=http://localhost:3000/callback`;

import {useRecoilState} from 'recoil';
import {userState, authState} from '../states';

const Login = () => {
  const navigate = useNavigate();
  const [user, setUser] = useRecoilState(userState);
  const [isLoggedIn, setIsLoggedIn] = useRecoilState(authState);
  const {
    colors: {
      neutral: {BLACK, DARK_GREY, GREY_BLUE, WHITE},
    },
  } = useTheme();

  const initializing = async () => {
    const token = localStorage.getItem('ACCESS_TOKEN');
    if (!token) {
      window.location.assign(GITHUB_END_POINT);
    }
    //     
    const {data} = await GithubRepository.getUser(token as string);
    if (data) {
      setUser(data);
      setIsLoggedIn(true);
      navigate('/');
      console.log(user);
    }
  };

  return (
    <S.Container>
      <Typography size={'24'} weight={'700'} color={BLACK} marginBottom={24}>
          MO:RE
      </Typography>
      <Typography size={'18'} weight={'500'} color={DARK_GREY} marginBottom={48}>
        Github       ?
      </Typography>
      <Button
        className="login-button"
        padding={'20px 90px'}
        backgroundColor={WHITE}
        borderRadius={'8'}
        borderColor={GREY_BLUE}
        onClick={() => initializing()}>
        <GithubIcon />
        <Typography size="13">Github  </Typography>
      </Button>
    </S.Container>
  );
};

export default Login;

const S = {
  Container: styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .login-button {
      display: flex;
      align-items: center;
      gap: 10px;
    }
  `,
};
