import Login from '../pages/Login';
import Profile from '../pages/Profile';
import Main from '../pages/Main';
import Callback from '../pages/Callback';
import UploadRepository from '../pages/UploadRepository';
import Detail from '../pages/Detail';

const pageRoutes: {
  path: string;
  isPublic: boolean;
  element: JSX.Element;
}[] = [
  {
    path: '/',
    isPublic: true,
    element: <Main />,
  },
  {
    path: '/callback',
    isPublic: true,
    element: <Callback />,
  },
  {
    path: '/login',
    isPublic: true,
    element: <Login />,
  },
  {
    path: '/profile',
    isPublic: true,
    element: <Profile />,
  },
  {
    path: '/post/new',
    isPublic: true, // false ,   true !
    element: <UploadRepository />,
  },
  {
    path: '/post/:id',
    isPublic: true, // false ,   true !
    element: <UploadRepository />,
  },
  {
    path: '/detail',
    isPublic: true,
    element: <Detail />,
  },
];

export default pageRoutes;
